
import Vue from 'vue'
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import MyPopupPicker from 'c%/PopupPicker'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'


export default{
mixins: [globals,common,wxjssdk],
components: {
MyPopupPicker,
Checklist,
XCircle,
Popup,
XButton,
Datetime,
XInput,
Group,
Cell,
Divider,
PopupRadio
},
data () {
return {
popup_students: false,
match_students:[],
bind_students:[],
schools: [],

action: 'add',
images: {
serverId: [],
localId: [],
urls: []
},
student: {
student_name: '',
birth_time: '',
photo_url: '',
school_id: 0,
school_class: '',
school_grade: '',
sex: '1',
first_tel: ''
}
}
},
mounted () {
this.student.first_tel = this.$store.state.user.info.mobile
this.get_schools()
this.init_students()
if(!this.$route.query.sid){
this.get_match_students()
}
},
methods: {
changeAvatar () {
this.chooseImage(1,true).then(()=>{
if(this.images.localId.length>0){
if(this.isIOS){
this.getlocalImageData(this.images.localId[0]).then(uri=>{
this.student.photo_url = uri
})
}else{
this.student.photo_url = this.images.localId[0]
}
}
})
},
select_school (school_id) {
this.student.school_id = school_id
},
init_students () {
let sid = this.$route.query.sid
if(sid){
this.action = 'edit'
this.sapi('get','students/'+sid)
.then(response=>{
this.student = response.body.data
this.student.birth_time = moment(this.student.birth_time).format('YYYY-MM-DD')
})
}
},
bind () {
this.popup_students = false
this.sapi('post','students/bind',{sids:this.bind_students})
.then(response=>{
this.toast('绑定成功','success')

setTimeout(()=>{
this.$router.go(-1)
},800)

}).catch(response=>{
this.alert('失败提示',response.body.message,true)
})
},
get_match_students () {
this.sapi('get','students/match')
.then(response=>{
let data = response.body.data.list
if(data.length > 0){
data.forEach(s=>{
this.match_students.push({
key: s.sid,
value: s.student_name
})
})
this.popup_students = true
}
})
},
get_schools () {
let public_schools = this.$store.state.gvars.public_schools

if(public_schools&&public_schools.length){
public_schools.forEach(s=>{
this.schools.push({
key: s.ps_id,
value: s.school_name
})
})
}

},
save () {
let [method,uri,msg] = (this.action === 'add' ? ['post','students','添加成功'] : ['put','students/'+this.student.sid,'修改成功'])
let param = util.copy(this.student)

delete param.create_time

if(this.images.localId.length>0&&this.images.urls.length>0){
param.photo_url = this.images.urls[0].file_url
}

this.sapi(method,uri,param)
.then(response=>{
this.toast(msg,'success')
setTimeout(()=>{
this.$router.replace({path:'./archives'})
},800)
}).catch(response=>{
this.alert('错误提示',response.body.message,true)
})
},
select_time (time) {
this.birth_time = time
},
},
computed: {
grades () {
let grades = this.dicts('grade'),
result = []
if(grades.length>0){
grades.forEach(g=>{
result.push({
key: parseInt(g.name),
value: g.title
})
})
}
return result
},
max_year () {
return parseInt(moment(new Date()).format('YYYY'))
},
upload_header(){
return {
'x-token':this.$store.state.user.token,
'x-file-key':'files'
}
},
upload_post(){
return {
mod:'student_upload'
}
},
}
}



